import React,{useEffect} from 'react'
import './about.css'
import ScrollReveal from 'scrollreveal';

const About = () => {

  useEffect(() => {
    const sr = ScrollReveal({
      distance: '50px',
      duration: 2000,
      reset: true
    });

    sr.reveal('.aboutMainDivImg', { delay: 200, origin: 'top' });
    sr.reveal('.aboutMainDivText', { delay: 200, origin: 'bottom' });
  }, []);

  return (
    <section id='about'>
      <div className="aboutMainDiv">
        <div className="aboutMainDivImg">
          <img src="https://gautamgohil.vercel.app/assets/aboutme.d047a9cb.svg" alt="" className='aboutImg' />
        </div>
        <div className='aboutMainDivText'>

          <h2 className="aboutTitle">Who I Am</h2>
          <h3 className="aboutMain">My name's Rahul Kumar. I'm a full stack web developer based in Patna, India.

            I am a BSc pursuing and love exploring new technologies in the field of Web Development and always try to adapt to them. I believe in the concept of continuous learning by regularly upgrading my skills and enhancing my knowledge. I have learnt MERN stack, data structures, algorithms, and soft skills at Masai School. And besides coding, I love Playing Cricket, and listeninng  music. As a developer, my hunger for learning has drastically increased. And I want to centralize my skills and learning for enhancement of an organization and more of it, to enhance my career.</h3>


        </div>
      </div>


    </section>
  )
}

export default About