import React,{useEffect} from 'react'
import './tools.css'
import ScrollReveal from 'scrollreveal';
import git from "../../assest/git.png";
import GitHub from "../../assest/GitHub.jpeg";
import NPM from "../../assest/NPM.jpeg";
import Netlify from "../../assest/Netlify.png";
import JsonServer from "../../assest/Json-Server.jpeg";
import VisualStudeoCode from "../../assest/Visual-Studeo-Code.jpeg";
import railway from "../../assest/Railway.svg";
import replit from "../../assest/replit.png";
import postman from "../../assest/postman.svg";

const Tools = () => {

    useEffect(() => {
        const sr = ScrollReveal({
          distance: '100px',
          duration: 2000,
          reset: true
        });
    
        sr.reveal('.toolBar', { delay: 200, origin: 'bottom' });
    
      }, []);

  return (
    <section id='tools'>
        <h1>TOOLS</h1>
        <div className='toolMain'>
                <div className='toolBar'>
                    <img src={git} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>Git</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={GitHub} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>GitHub</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={NPM} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>NPM</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={Netlify} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>Netlify</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={JsonServer} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>Json-Server</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={VisualStudeoCode} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>VS-Code</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={railway} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>Railway</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={replit} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>Replit</h2>
                    </div>
                </div>
                <div className='toolBar'>
                    <img src={postman} alt="" className="toolImg" />
                    <div className='toolText'>
                        <h2>Postman</h2>
                    </div>
                </div>
        </div>
    </section>
  )
}

export default Tools