import React, { useEffect, useRef } from 'react'
import './contact.css'
import ScrollReveal from 'scrollreveal';
import { IoLogoLinkedin } from "react-icons/io5";
import { FaGithub } from "react-icons/fa";
import { IoIosMailOpen } from "react-icons/io";
import { IoCall } from "react-icons/io5";
import emailjs from '@emailjs/browser';
import '@fortawesome/fontawesome-free/css/all.css';


const Contact = () => {

   const form = useRef();
   const sendEmail = (e) => {
      e.preventDefault();

      emailjs
         .sendForm('service_g5j5cfq', 'template_pxyn5me', form.current, {
            publicKey: 'GXp7YdiuXlMLdxlkd',
         })
         .then(
            (result) => {
               console.log('SUCCESS!', result.text);
               e.target.reset()
               alert("Email Send Successfully")
            },
            (error) => {
               console.log('FAILED...', error.text);
            },
         );
   };

   useEffect(() => {
      const sr = ScrollReveal({
         distance: '50px',
         duration: 2000,
         reset: true
      });

      sr.reveal('.li1', { delay: 200, origin: 'top' });
      sr.reveal('.li2', { delay: 200, origin: 'bottom' });
      sr.reveal('.formInp', { delay: 200, origin: 'bottom' });
      // sr.reveal('.contact4', { delay: 100, origin: 'bottom' });

   }, []);

   return (
      <section id='contact'>
         <h1 className='contactHead'>CONTACT</h1>
         <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.6.3/css/all.css" integrity="sha384-UHRtZLI+pbxtHCWp1t77Bi1L4ZtiqrqD80Kn4Z8NTSRyMA2Fd33n5dQ8lWUE00s/"  />

         <div className='contactFormMain'>
            <div className="contactIcon">
               <ul>
                  <li className='li1'>
                     <a href="https://github.com/Rahulkrsharma2004" target="_blank">
                        <i className="fab fa-github icon" ></i>    </a>
                  </li>
                  <li className='li1'>
                     <a href="https://twitter.com/RahulKu42133995" target='_blank'><i className="fab fa-twitter icon"></i></a>
                  </li>
                  <li className='li2'>
                     <a href="https://www.linkedin.com/in/rahul-kr-sharma-35a989280" target="_blank"><i className="fab fa-linkedin-in icon"></i></a></li>
                  <li className='li2'>
                     <a href="https://www.youtube.com/channel/UCecfGic_AeML8NmcurVNxVw" target='_blank'><i className="fab fa-youtube icon"></i></a></li>
               </ul>
            </div>
            <p style={{ textAlign: "center" }}>Please fill out the form below to discuss any work opportunities</p>
            <br />
            <form ref={form} onSubmit={sendEmail}>
               <input type="text" placeholder='Your Name' name='from_name' className='formInp'/>
               <br />
               <input type="email" placeholder='Your Email' name='from_email' className='formInp'/>
               <br />
               <textarea name="message" id="" cols="30" rows="10" placeholder='Your Message' className='formInp'></textarea>
               <br />
               <button type='submit' value="send">SUBMIT</button>
               <br />
            </form>
         </div>

         <h3 style={{ backgroundColor: "gold", color: "black", textAlign: "center" }}>Thank You for visiting my PortFolio </h3>
      </section>
   )
}

export default Contact