import React,{useEffect} from 'react'
import ScrollReveal from 'scrollreveal';
import './skill.css'
import html from "../../assest/html.jpeg";
import css from "../../assest/css.jpeg";
import js from "../../assest/js.jpeg";
import react from "../../assest/react.jpeg";
import node from "../../assest/node.jpeg";
import typescript from "../../assest/typescript.jpeg";
import styled from "../../assest/styled.jpeg";
import reactRouter from "../../assest/reactRouter.jpeg";
import chakra from "../../assest/chakra.jpeg";
import mongodb from "../../assest/mongodb.png";
import express from "../../assest/express.png";
import tailwind from "../../assest/tailwind.png";

const Skill = () => {


    useEffect(() => {
        const sr = ScrollReveal({
          distance: '100px',
          duration: 2000,
          reset: true
        });
    
        sr.reveal('.skillBar', { delay: 200, origin: 'bottom' });
    
      }, []);


    return (
        <section id='skill'>
            <h1 className='skillHead'>SKILLS</h1>
            <div className='skillMain'>

                <div className='skillBar'>
                    <img src={html} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>HTML</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={css} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>CSS</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={js} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>JavaScript</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={react} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>React.Js</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={node} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>Node.Js</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={mongodb} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>MongoDB</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={express} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>Express.js</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={typescript} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>TypeScript</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={styled} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>Styled-Components</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={reactRouter} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>React-Router-Dom</h2>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={chakra} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>Chakra-UI</h2>
                    </div>
                </div>
                
                <div className='skillBar'>
                    <img src={tailwind} alt="" className="skillImg" />
                    <div className='skillText'>
                        <h2>Tailwind</h2>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Skill