import React, { useState, useEffect } from 'react'

import './intro.css'
import ScrollReveal from 'scrollreveal'
import rahul1 from '../../assest/Rahul1.jpg'
import helloimg from '../../assest/helloimg.png'
import RahulResume from '../../assest/RahulResume.pdf'

const Intro = () => {

  const [displayText, setDisplayText] = useState('');
  const originalText = "Full Stack Devloper";
  const speed = 100;


  useEffect(() => {

    let currentIndex = 0;

    const intervalId = setInterval(() => {
      setDisplayText(originalText.substring(0, currentIndex));
      currentIndex += 1;

      if (currentIndex > originalText.length) {
        clearInterval(intervalId);
      }
    }, speed);

    return () => {
      clearInterval(intervalId);
    };
  }, [originalText, speed]);

  const handleDownload =  () => {
    window.open(RahulResume, '_blank');

    const link = document.createElement('a');

    link.href = RahulResume;

    link.download = 'Rahul-Kumar-Resume';

    document.body.appendChild(link);

    link.click();

    document.body.removeChild(link);
  };

  useEffect(() => {
    const sr = ScrollReveal({
      distance: '200px',
      duration: 2000,
      reset: true
    });

    sr.reveal('.photo', { delay: 200, origin: 'top' });
    sr.reveal('.introTextContentDiv', { delay: 200, origin: 'bottom' });
  }, []);

  return (
    <section id="intro">
      <div className="introContent">
        <div className='introTextContentDiv'>
          <span className='hello'>Hello,<img src={helloimg} alt="" className='helloimg' /></span><br />
          <span className="introText">I'm <span className='introName'>RAHUL</span> <br />{displayText}</span>
          <p className='introPara'>An enthusiastic <span style={{ color: "red", fontWeight: "bold", fontSize:"1.2rem" }}>MERN Stack Web Developer</span> with a strong set of technical  as well as non-technical skills and a dedication towards creating useful and interactive web applications.</p>
          <a href={RahulResume} target="_blank" rel="noopener noreferrer" className='navbtn'><button className='introResumeBtn' onClick={()=>{handleDownload()}}><img src='https://cdn4.iconfinder.com/data/icons/arrows-245/24/download_1-1024.png' alt="" className='btnImg'/> Resume </button></a> 
        </div>
        <div className='introProfileImgDiv'>
          <img src={rahul1} alt="Profile" className='photo' />
        </div>
      </div>

    </section>
  )
}

export default Intro


