import React, { useState } from 'react'
import './navbar.css'
import rahul1 from '../../assest/rahul1.png'
import { Link } from 'react-scroll'
import menu from '../../assest/menu.png'
import RahulResume from '../../assest/RahulResume.pdf'

const Navbar = () => {
  const [showMenu, setShowMenu] = useState(false)

  const handleDownload = () => {
    window.open(RahulResume, '_blank');

    const link = document.createElement('a');

    link.href = RahulResume;

    link.download = 'Rahul-Kumar-Resume';

    document.body.appendChild(link);

    link.click();

    document.body.removeChild(link);
  };

  return (
    <nav className='navbar'>
      <img src={rahul1} alt="logo" className='logo' />
      <div className="desktopMenu">
        <Link activeClass='active' to='intro' spy={true} smooth={true} offset={-100} duration={900} className='desktopMenuListItem'>Home</Link>
        <Link activeClass='active' to='about' spy={true} smooth={true} offset={-100} duration={900} className='desktopMenuListItem'>About</Link>
        <Link activeClass='active' to='skill' spy={true} smooth={true} offset={-100} duration={900} className='desktopMenuListItem'>Skills</Link>
        <Link activeClass='active' to='project' spy={true} smooth={true} offset={-100} duration={900} className='desktopMenuListItem'>Project</Link>
        <Link activeClass='active' to='contact' spy={true} smooth={true} offset={-100} duration={900} className='desktopMenuListItem'>Contact</Link>
      </div>
      <a href={RahulResume} target="_blank" rel="noopener noreferrer" className='navbtn'><button className='navbarResumeBtn' onClick={() => { handleDownload() }}><img src='https://cdn4.iconfinder.com/data/icons/arrows-245/24/download_1-1024.png' alt="" className='btnImg' /> Resume </button></a>
      
      <img src={menu} alt="Menu" className='mobMenu' onClick={() => setShowMenu(!showMenu)} />
      <div className="navMenu" style={{ display: showMenu ? "flex" : "none" }} >
        <Link activeClass='active' to='intro' spy={true} smooth={true} offset={-100} duration={800} className='listItem' onClick={() => setShowMenu(false)} >Home</Link>
        <Link activeClass='active' to='about' spy={true} smooth={true} offset={-100} duration={800} className='listItem' onClick={() => setShowMenu(false)} >About</Link>
        <Link activeClass='active' to='skill' spy={true} smooth={true} offset={-100} duration={800} className='listItem' onClick={() => setShowMenu(false)} >Skills</Link>
        <Link activeClass='active' to='project' spy={true} smooth={true} offset={-100} duration={800} className='listItem' onClick={() => setShowMenu(false)} >Project</Link>
        <Link activeClass='active' to='contact' spy={true} smooth={true} offset={-100} duration={800} className='listItem' onClick={() => setShowMenu(false)} >Contact</Link>
      </div>
    </nav>
  )
}

export default Navbar


