import React, { useEffect } from 'react'
import './project.css'
import ScrollReveal from 'scrollreveal'
import tmetricimg from '../../assest/tmetricimg.png'
import myntraimg from '../../assest/myntraimg.png'
import internship from '../../assest/internship.png'

const Project = () => {

  useEffect(() => {
    const sr = ScrollReveal({
      distance: '100px',
      duration: 2000,
      reset: true
    });


    sr.reveal('.projectItem', { delay: 200, origin: 'bottom' });
  }, []);


  return (
    <section id='project'>
      <h1>PROJECT</h1>
      <div className='projectMain'>
        <div className="projectItem">
          <img src={tmetricimg} alt="tmetric" className='tmetricimg' />
          <h2 style={{ textAlign: "center" }}>Tmetric Website Clone</h2><br />
          <h3><span style={{ color: "yellow" }}>ABOUT</span> - TMetric by Devart: your best business companion for breakthrough results and its time tracking website
            Powerful time tracker that lets you focus on work that 
            matters. </h3>
          <br />
          <h3><span style={{ color: "yellow" }}>TECH STACK</span>  - React | HTML | CSS | NodeJS | JavaScript | Git </h3>
          <br />
          <div className='projectBtn'>
            <a href="https://rahultmetric.netlify.app" target="_blank" rel="noopener noreferrer" ><button className='btnweb'>Website</button></a>
            <a href="https://github.com/masai-course/rahul_kumar_cap05_218/tree/master/tech/Project-Of-Tmetric" target="_blank" rel="noopener noreferrer" ><button className='btngit'>Github</button></a>
          </div>

        </div>

        <div className="projectItem">
          <img src={myntraimg} alt="Myntra" className='tmetricimg' />
          <h2 style={{ textAlign: "center" }}>Myntra Website Clone</h2><br />
          <h3><span style={{ color: "yellow" }}>ABOUT</span> -  Myntra is a one stop shop for all your fashion and lifestyle needs. Being India's largest e-commerce store for fashion and lifestyle products, Myntra aims at providing a hassle free.</h3><br />
          <h3><span style={{ color: "yellow" }}>TECH STACK</span> - | HTML | CSS | JS | React | MongoDB | Node | Express |</h3>
          <br />
          <div className='projectBtn'>
            <a href="https://myntra-frontend-app.netlify.app" target="_blank" rel="noopener noreferrer" ><button className='btnweb'>Website</button></a>
            <a href="https://github.com/Rahulkrsharma2004/Myntra-Full-App-Clone" target="_blank" rel="noopener noreferrer" ><button className='btngit'>Github</button></a>
          </div>
        </div>

        <div className="projectItem">
          <img src={internship} alt="internship project" className='tmetricimg' />
          <h2 style={{ textAlign: "center" }}>Internship Project</h2><br />
          <h3><span style={{ color: "yellow" }}>ABOUT</span> - This website of internship project from togglehead company and inside this assignment feature slider image and number animation and FAQ section.</h3>
          <br />
          <h3><span style={{ color: "yellow" }}>TECH STACK</span> - | HTML | CSS | JS | React | NPM | Node |</h3>
          <br />
          <div className='projectBtn'>
            <a href="https://toggleheadrahulassignment.netlify.app" target="_blank" rel="noopener noreferrer" ><button className='btnweb'>Website</button></a>
            <a href="https://github.com/Rahulkrsharma2004/togglehead/tree/main/rahul_internship" target="_blank" rel="noopener noreferrer" ><button className='btngit'>Github</button></a>
          </div>
        </div>


        <div className="projectItem">
          <img src={myntraimg} alt="Myntra" className='tmetricimg' />
          <h2 style={{ textAlign: "center" }}>Myntra Website Clone</h2><br />
          <h3><span style={{ color: "yellow" }}>ABOUT</span> -  Myntra is a one stop shop for all your fashion and lifestyle needs. Being India's largest e-commerce store for fashion and lifestyle products, Myntra aims at providing a hassle free.</h3><br />

          <h3><span style={{ color: "yellow" }}>TECH STACK</span> - | HTML | CSS | JS | React | MongoDB | Node | Express |</h3>
          <br />
          <div className='projectBtn'>
            <button className='btnweb'>Website</button>
            <button className='btngit'>Github</button>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Project